<template>
  <div>
    <FullCalendar
      ref="fullCalendar"
      :options="calendarOptions"
      :events="events"
      :eventContent="eventRender"
    />
  </div>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
export default {
  name: "LichHopThang",
  components: {
    FullCalendar,
  },
  data() {
    return {
      calendarOptions: {
        plugins: [dayGridPlugin, interactionPlugin],
        initialView: "dayGridMonth",
        locale: "vi",
        firstDay: 1,
        titleFormat: { year: "numeric", month: "short" },
        headerToolbar: { right: "prev,next" },
        eventOrder: ["title"],
        // events: this.events,
        eventTimeFormat: { hour: "2-digit", minute: "2-digit" },
        eventContent: this.eventRender,
      },
    };
  },
  computed: {
    events() {
      if (this.$store.getters["schedule/getSchedules"]) {
        console.log(
          "events -> this.$store.getters",
          this.$store.getters["schedule/getSchedules"].dataNotification
        );
        return this.$store.getters["schedule/getSchedules"].dataNotification;
      }
      return [];
    },
  },
  methods: {
    eventRender(eventInfo) {
      return (
        <a class="fc-daygrid-event fc-daygrid-dot-event fc-event fc-event-start fc-event-end fc-event-past">
          <div
            class="fc-daygrid-event-dot"
            style={{
              borderColor: `#${eventInfo.event._def.extendedProps.status_color}`,
            }}
          ></div>
          <div class="fc-event-title">{eventInfo.event.title}</div>
          <div class="fc-event-time">{eventInfo.timeText}</div>
        </a>
      );
    },
  },
};
</script>

<style lang="scss" scoped>
